// This is the important part!

function collapseSection(element) {
  // get the height of the element's inner content, regardless of its actual size
  var sectionHeight = element.scrollHeight;

  // temporarily disable all css transitions
  var elementTransition = element.style.transition;
  element.style.transition = "";

  // on the next frame (as soon as the previous style change has taken effect),
  // explicitly set the element's height to its current pixel height, so we
  // aren't transitioning out of 'auto'
  requestAnimationFrame(function () {
    element.style.height = sectionHeight + "px";
    element.style.transition = elementTransition;

    // on the next frame (as soon as the previous style change has taken effect),
    // have the element transition to height: 0
    requestAnimationFrame(function () {
      element.style.height = 0 + "px";
    });
  });

  // mark the section as "currently collapsed"
  element.setAttribute("data-collapsed", "true");
}

function expandSection(element) {
  // get the height of the element's inner content, regardless of its actual size
  var sectionHeight = element.scrollHeight;

  // have the element transition to the height of its inner content
  element.style.height = sectionHeight + "px";

  // when the next css transition finishes (which should be the one we just triggered)
  element.addEventListener(
    "transitionend",
    function (e) {
      // remove "height" from the element's inline styles, so it can return to its initial value
      element.style.height = null;
    },
    { once: true }
  );

  // mark the section as "currently not collapsed"
  element.setAttribute("data-collapsed", "false");
}

$(".order-header-right .fa-chevron-down").on("click", function () {
  var section = $(this).parent().parent().parent().find(".order-body")[0];

  var isCollapsed = section.getAttribute("data-collapsed") === "true";

  if (isCollapsed) {
    expandSection(section);
    section.setAttribute("data-collapsed", "false");
  } else {
    collapseSection(section);
  }
});

$(".order-row-header")
  .find(".fa-chevron-down")
  .on("click", function () {
    var section = $(this).parent().parent().parent().find(".order-row-body")[0];
    var isCollapsed = section.getAttribute("data-collapsed") === "true";

    if (isCollapsed) {
      expandSection(section);
      section.setAttribute("data-collapsed", "false");
    } else {
      collapseSection(section);
    }
  });

$(".order-right-div").each(function () {
  var amount = 97;

  for (let i = 0; i < amount; i++) {
    if (i == 27) {
      var t = $("<div class='grid-item grid-item-border-right'></div>");
    } else if (i < 28 || i >= 69) {
      var t = $("<div class='grid-item'></div>");
    } else if (i == 28) {
      var t = $(`<div class='grid-item start-action-1 light-letter'>
      <span>Handeling: p14 Bolchrysant CC/DC los/tray R T MK 200 potten</span>
      <span>Locatie: ew4</span>
      </div>`);
    } else if (i == 50) {
      var t = $(`<div class='grid-item start-action-2 light-letter'>
      <span>Handeling: Pendel transport</span>
      <span>Locatie: ew4</span>
      </div>`);
    }

    $(this).append(t);
  }
});

